* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: sans-serif;
}
.logo {
  font-size: 30px;
}
.spinner {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 7px solid rgba(30, 143, 255, 0.534);
  border-top-color: dodgerblue;
  animation: loader 1s infinite linear;
  margin: 50px auto;
}
@keyframes loader {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
